import {checkGoogleAuthenticator} from "@/apis/user";

export const verifySecuretedCode = vm => {
  vm.$toast('请输入资金密码')
  return new Promise((resolve, reject) => {
    vm.$root.showPassowordKeyboard = true
    vm.$root.$once('root:global-password', async code => resolve(code))
  })
}

export const verifyGoogleAuth = (vm) => {
  if (vm.$root.userInfo && vm.$root.userInfo.isGoogleAuthenticator) {
    vm.$toast('请输入Google安全码')
    return new Promise((resolve, reject) => {
      setTimeout(() => {
        vm.$root.showPassowordKeyboard = true
        vm.$root.$once('root:global-password', async code => {
          if (!code || code === 'close-by-route-change') return resolve(false)
          const {ok, msg} = await checkGoogleAuthenticator({safeCode: code})
          if (ok) {
            resolve(true)
          } else {
            resolve(false)
            vm.$toast.fail(msg)
          }
        })
      }, 0)
    })
  } else {
    return Promise.resolve(true)
  }

}
