        <div class="description">
          该用户为平台认证用户，付款信息暂不展示，请卖家查询到款后 放心确认转币
        </div>
        <div class="certificated">
          <!-- <span>已缴纳保证金</span> <span class="amount">999,999 V币</span> -->
        </div>
        <div class="certification"></div>

        <!-- 卖家收款信息 -->
        <components
          :is="componentsMapping[radio]"
