          title="卖方收款信息"
        >
        </components>

        <div class="riskDescription gray-8">
          <div class="title">{{$root.title}}风控</div>
          <div>
            1、为了降低您的交易风险，认证用户已向平台缴纳保证金，请放心 交易。
          </div>
          <div>
            2、平台认证用户，由平台担保交易资金安全性，如出现资金问题由
