          </div>
        </div>
      </div>
    </template>
    <template v-else>
      <!-- 买家收款信息 -->
      <components
        :is="componentsMapping[radio]"
        :hide="hide"
        :payment="hide ? undefined : buyerPayment"
        title="买方付款信息"
      >
        <!-- <span
        slot="title-right"
        class="countdown"
        v-if="status == TRANSACTING_ORDER_STATUS.BUYER_REQUEST"
      >
        订单倒计时
        <van-count-down :time="countdown" @finish="onCountdownFinish" />
      </span> -->
      </components>

      <template v-if="status == TRANSACTING_ORDER_STATUS.BUYER_REQUEST">
        <div class="gray-8"></div>
        <div class="seller-credit-indicator">
          <div class="title">买方信用资讯</div>
          <div class="row">
            <div class="col">
              <div class="label">{{ buyerOrderIndicator.range }}日成单数</div>
              <div class="value">
                <span class="bold">
                  {{ buyerOrderIndicator.totalSuccessfulOrder }}
                </span>
              </div>
            </div>
            <div class="col">
              <div class="label">{{ buyerOrderIndicator.range }}日成单率</div>
              <div class="value">
                <span class="bold">
                  {{ buyerOrderIndicator.totalSuccessRate }}%
                </span>
              </div>
            </div>
          </div>
          <div class="row">
            <div class="col">
              <div class="label">
                {{ buyerOrderIndicator.range }}日平均放币时间
              </div>
              <div class="value">
                &lt;
                <span class="bold">
                  {{ buyerOrderIndicator.sellerAvgTime }}
                </span>
                分钟
              </div>
            </div>
            <div class="col">
              <div class="label">
                {{ buyerOrderIndicator.range }}日平均付款时间
              </div>
              <div class="value">
                &lt;
                <span class="bold">
                  {{ buyerOrderIndicator.buyerAvgTime }}
                </span>
                分钟
              </div>
            </div>
          </div>
          <div class="row">
            <div class="col">
              <div class="label">买单</div>
              <div class="value">
                <span class="bold">
                  {{ buyerOrderIndicator.buyerOrder }}
                </span>
                笔
              </div>
            </div>
            <div class="col">
              <div class="label">卖单</div>
              <div class="value">
                <span class="bold">
                  {{ buyerOrderIndicator.sellerOrder }}
                </span>
                笔
              </div>
            </div>
          </div>
        </div>
      </template>
      <!-- 卖家收款信息 -->
      <components
        :is="componentsMapping[radio]"
        v-else
        :hide="hide"
        :payment="hide ? undefined : sellerPayment"
        title="卖方收款信息"
      >
      </components>

      <!-- 买方凭证 -->
      <template
        v-if="
          status == TRANSACTING_ORDER_STATUS.TRANSFERRED ||
          (status == TRANSACTING_ORDER_STATUS.SELLER_DID_NOT_RECEIVE &&
            !isCountdownEnd) ||
          status == TRANSACTING_ORDER_STATUS.DISPUTING
        "
      >
        <van-image
          :src="$root.getImageUrl(orderInfo.voucher)"
          class="certificationImage"
          fit="contain"
          @click="previewImage($root.getImageUrl(orderInfo.voucher))"
        >
        </van-image>
      </template>
      <!-- 买方凭证 -->

      <!-- 提示 -->
      <template
        v-if="
          status == TRANSACTING_ORDER_STATUS.WAITING_FOR_TRANSFER ||
          status == TRANSACTING_ORDER_STATUS.TRANSFERRED
        "
      >
        <div class="warning">
          1.请认真核对买方付款信息，如「到款信息与买方付款信息不一
          致」或「未收到付款」千万不要放行您的数字货币，一旦确认我已
          收到款后转币给您造成的损失平台概不负责
        </div>
        <div class="warning">2.收到买方真实付款后点击「确认转币」</div>
      </template>
      <!-- 提示 -->
    </template>

    <div class="btns">
      <!-- <template v-if="status == TRANSACTING_ORDER_STATUS.HANGING">
        <van-button class="danger block" @click="cancel_hanging">
          取消挂单
        </van-button>
      </template> -->
      <template v-if="status == TRANSACTING_ORDER_STATUS.BUYER_REQUEST">
