          取消交易
        </van-button>
        <van-button class="primary block" @click="start_transaction">
          开始交易
        </van-button>
      </template>
      <template
        v-if="
          status == TRANSACTING_ORDER_STATUS.WAITING_FOR_TRANSFER ||
          status == TRANSACTING_ORDER_STATUS.TRANSFERRED
        "
      >
        <van-button
          :class="{
            disabled: status != TRANSACTING_ORDER_STATUS.TRANSFERRED,
          }"
          class="danger block"
          @click="did_not_received"
        >
          未收到转账
        </van-button>
        <van-button
          :class="{
            disabled: status != TRANSACTING_ORDER_STATUS.TRANSFERRED,
          }"
          class="primary block"
          @click="confirm_transfer"
        >
          确认转币
        </van-button>
      </template>
      <template v-if="status == TRANSACTING_ORDER_STATUS.DISPUTING">
