      :class="{disabled:!enable_receive_transfer_dialog}"
      confirm-button-color="#6f59fe"
      :confirm-button-text="!enable_receive_transfer_dialog ? `${countdown_time}s` : '是'"
      cancel-button-text="否"
      show-cancel-button
      title="重要提醒"
      :before-close="confirmed_transfer"
      @close="enable_receive_transfer_dialog = false">
