      <span style="color:#FB2222;font-weight: bold;">「收款金额」</span>是否与您的卖币金额一致
      <van-count-down
        v-show="false"
        v-if="show_receive_transfer_confirm"
        time="5000"
        format="ss"
        @finish="countdownFinish"
        @change="countdownChange"
      />
    </van-dialog>
