<script>
import {PAYMENT_METHOD, STATUS_TYPE, STEP, TRANSACTING_ORDER_STATUS,} from "@/vendor/enum";
import {
  is_cancel_transaction,
  is_didnt_receive_transfer,
  is_start_transaction,
} from "./components/dialogs";
import {AliPaymentInfo, BankPaymentInfo, WechatPaymentInfo,} from "./components";
import {
  getCancelsOrderDailyCount,
  getCurrentOrderStatus,
  myOrderDetail,
  sellerCompletesOrder,
  sellerConfirmsToCreateOrder,
  sellerHasNotReceivedPayment,
  sellerRefusesToCreateOrder,
} from "@/apis/market";
import {ImagePreview} from "vant";
import { verifyGoogleAuth, verifySecuretedCode } from '@/vendor/globalVerify';
import { cryptoMerchantMixins } from '@/vendor/mixins';

// 卖方单状态
/**
 * HANGING
 * BUYER_REQUEST
 *
 * SELLER_CANCELED_DEAL
 * WAITING_FOR_TRANSFER
 *
 * BUYRER_CANCELD_DEAL
 * TRANSFERRED
 * TIME_OVER
 *
 * SELLER_DID_NOT_RECEIVE
 * DISPUTING
 * CANCELED_BY_OFFICIAL
 * CONFIRMED_BY_OFFICIAL
 * DEAL
 *
 */
export default {
  components: {BankPaymentInfo, WechatPaymentInfo, AliPaymentInfo},
  mixins: [cryptoMerchantMixins],
  data() {
    return {
      PAYMENT_METHOD,
      STATUS_TYPE,
      STEP,
      TRANSACTING_ORDER_STATUS,
      status: TRANSACTING_ORDER_STATUS.HANGING,
      countdown: 1,
      isTimeOut: false,
      show_apply: false,
      applyReason: "",
      loading: false,
      radio: "",
      orderInfo: {},
      buyerPayment: {},
      sellerPayment: {},
      buyerOrderIndicator: {},
      componentsMapping: {
        [PAYMENT_METHOD.BANK]: "BankPaymentInfo",
        [PAYMENT_METHOD.ALI]: "AliPaymentInfo",
        [PAYMENT_METHOD.WE_CHAT]: "WechatPaymentInfo",
      },
      show_receive_transfer_confirm: false,
      enable_receive_transfer_dialog: false,
      countdown_time: 0
    };
  },
  computed: {
    hide() {
      return !(
        (this.status === this.TRANSACTING_ORDER_STATUS.TRANSFERRED &&
          !this.isCountdownEnd) ||
        this.status === this.TRANSACTING_ORDER_STATUS.DISPUTING
      );
    },
    isCountdownEnd() {
      return this.countdown <= 0;
    },
    stepsActive() {
      return this.STEP[this.status];
    },
    isTimeoutDisputing() {
      if (
        this.status == this.TRANSACTING_ORDER_STATUS.DISPUTING &&
        this.isTimeOut
      ) {
        return true;
      }
      return false;
    },
    payments() {
      return this.$root.parsePayments(this.orderInfo.paymentType);
    },
    isSystemBuyer() {
      return this.orderInfo.buyerType === "System";
    },
  },
  created() {
    this.load();
    this.$root.$emit("root:set-topbar", ["left-back", "customer-service"]);
  },
  beforeDestroy() {
    clearTimeout(this.timer);
  },
  methods: {
    transactingOrderStatus(...args) {
      return this.$options.filters.transactingOrderStatus(...args);
    },
    start_transaction() {
      is_start_transaction().then(() => {
        sellerConfirmsToCreateOrder({orderId: this.$route.params.id}, this.ApiPath).then(
          ({ok, msg}) => {
            if (ok) {
              this.$toast.success(msg);
              this.status = TRANSACTING_ORDER_STATUS.WAITING_FOR_TRANSFER;
              this.load();
            } else {
              this.$toast.fail(msg);
            }
          }
        );
      });
    },
    cancel_transaction() {
      getCancelsOrderDailyCount(undefined, this.ApiPath).then(({ok, data, msg}) => {
        if (ok) {
          is_cancel_transaction(
            data.allowedCancelsOrderDailyCount - data.cancelsOrderDailyCount,
            data.allowedCancelsOrderDailyCount
          ).then(() => {
            sellerRefusesToCreateOrder({orderId: this.$route.params.id}, this.ApiPath).then(
              ({ok, msg}) => {
                if (ok) {
                  this.$toast.success(msg);
                  this.status = TRANSACTING_ORDER_STATUS.SELLER_CANCELED_DEAL;
                  this.load();
                } else {
                  this.$toast.fail(msg);
                }
              }
            );
          });
        } else {
          this.$toast.fail(msg);
        }
      });
    },
    did_not_received() {
      is_didnt_receive_transfer().then(() => {
        sellerHasNotReceivedPayment({orderId: this.$route.params.id}, this.ApiPath).then(
          ({ok, msg}) => {
            if (ok) {
              this.$toast.success(msg);
              this.status = TRANSACTING_ORDER_STATUS.SELLER_DID_NOT_RECEIVE;
              this.load();
            } else {
              this.$toast.fail(msg);
            }
          }
        );
      });
    },
    async confirmed_transfer(action, done) {
      done()
      if (action === 'confirm') {
        // 验证资金密码
        // 验证google验证码
        const securePassword = await verifySecuretedCode(this)
        const pass2 = await verifyGoogleAuth(this)
        if (!securePassword || !pass2) return;
        sellerCompletesOrder({orderId: this.$route.params.id, securePassword}, this.ApiPath).then(
          ({ok, msg}) => {
            if (ok) {
              this.$toast.success(msg);
              this.status = TRANSACTING_ORDER_STATUS.DEAL;
              this.load();
            } else {
              this.$toast.fail(msg);
            }
          }
        );
      }
    },
    countdownChange(time) {
      this.countdown_time = time.seconds
    },
    countdownFinish() {
      this.enable_receive_transfer_dialog = true
    },
    confirm_transfer() {
      this.show_receive_transfer_confirm = true
    },
    load() {
      this.loadDetails(this.$route.params.id);
    },
    loadDetails(orderId) {
      if (this.loading) return;
      this.loading = true;
      myOrderDetail({orderId}, this.ApiPath).then(({ok, msg, data}) => {
        this.loading = false;
        if (ok) {
          clearTimeout(this.timer);
          this.orderInfo = data.order;
          this.sellerPayment = data.sellerPayment;
          this.buyerPayment = data.buyerPayment;
          this.radio = data.order.paymentType;
          this.buyerOrderIndicator = data.buyerOrderIndicator;
          // 争议订单
          if (this.orderInfo.appeal > 0) {
            this.status = TRANSACTING_ORDER_STATUS.DISPUTING;
            if (this.orderInfo.status === TRANSACTING_ORDER_STATUS.TIME_OVER) {
              this.isTimeOut = true;
            }
          } else {
            this.status = this.orderInfo.status;
          }
          if (data.currentStateExpireTime) {
            if (data.currentStateExpireTime.expire) {
              this.countdown =
                data.currentStateExpireTime.expire * 1000 -
                this.$root.getServerTime();
            } else {
              this.countdown = 0;
            }
          }
          this.refreshStatus();
        } else {
          this.$toast.fail(msg);
        }
      });
    },
    refreshStatus() {
      this.timer = setTimeout(() => {
        getCurrentOrderStatus({orderId: this.$route.params.id}, this.ApiPath).then(
          ({ok, data}) => {
            this.refreshStatus();
            if (ok) {
              const old_status = this.status;
              // 争议订单
              if (this.orderInfo.appeal > 0) {
                this.status = TRANSACTING_ORDER_STATUS.DISPUTING;
                if (data.status === TRANSACTING_ORDER_STATUS.TIME_OVER) {
                  this.isTimeOut = true;
                }
              } else {
                this.status = data.status;
              }
              if (this.status !== old_status) {
                this.loadDetails(this.orderInfo.orderId);
              }
            }
          }
        );
      }, 10 * 1000);
    },
    onCountdownFinish() {
      this.load();
    },
    previewImage(path) {
      ImagePreview([path]);
    },
  },
};
</script>

