import {Dialog} from 'vant';

export const dialog_generator = (title, message) => new Promise((resolve, reject) => {
  Dialog.confirm({
    title,
    message,
    confirmButtonText: '是',
    cancelButtonText: '否',
    confirmButtonColor: '#6f59fe',
    allowHtml: true,
    beforeClose(action, done) {
      done();
      if (action === 'confirm') {
        resolve()
      } else {
        reject()
      }
    }
  })
  .catch(err => {})
})


export const cancel_hanging_order = (lease_cancel_time) => dialog_generator("是否取消挂单",
  `<div>
      <div class="tip">
        提示：
      </div>
      <div>
          1.如果你在一天内取消<span style="color:#FB2222">3次</span>挂单，当天你的账户将会被停止买卖。
      </div>
      <div class="al-c">
          今日还可取消
          <span style="color:#FB2222">${lease_cancel_time}次</span>
          挂单，请谨慎操作
      </div>
    </div>
`)

export const is_start_transaction = () => dialog_generator('是否开始交易', `<div>
开始交易后可在“交易记录”，“订单”中
「我的卖单」完成后续操作
</div>`)

export const is_cancel_transaction = (lease_cancel_time, maxium_cancelable_time = 3) => dialog_generator("是否取消该笔交易",
  `<div>
      <div class="tip">
        提示：
      </div>
      <div>
          1.如果你在一天内取消<span style="color:#FB2222">${maxium_cancelable_time}次</span>订单，当天你的账户将会被停止买卖。
      </div>
      <div class="al-c">
          今日还可取消
          <span style="color:#FB2222">${lease_cancel_time}次</span>
          订单，请谨慎操作
      </div>
    </div>
`)

